import Vue from 'vue'
import Router from 'vue-router'

// 按需加载
const _import = file => () => import ('@/views/' + file + '.vue').then(m => m.default)

const routerMap = [
  {
    path: '/',
    redirect: '/home'
  },
  {
    path: '/home',
    component: _import('home/index')
  },
  {
    path: '/pdf',
    component: _import('pdf/index')
  },
  { path: '/404', component: _import('error/404')},
  { path: '*', redirect: '/404'}
]
Vue.use(Router)

export default new Router({
  // mode: 'history',
  scrollBehavior: () => ({ y: 0 }),
  routes: routerMap
})



